.ripple {
  position: absolute;
  z-index: 10;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  opacity: 1;
  -webkit-transform: scale(0);
          transform: scale(0);
}

.ripple.is-reppling {
  -webkit-animation: rippleAnimate .6s linear;
          animation: rippleAnimate .6s linear;
}

.ripple-parent {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

@-webkit-keyframes rippleAnimate {
  100% {
      opacity: 0;
      -webkit-transform: scale(3);
              transform: scale(3);
  }
}

@keyframes rippleAnimate {
  100% {
      opacity: 0;
      -webkit-transform: scale(3);
              transform: scale(3);
